import Vue from "vue";
import App from "./App.vue";
import ElementUI from "element-ui";        // 导入 element-ui
import "element-ui/lib/theme-chalk/index.css"; // 导入 element-ui 样式
import service from "./utils/service.js";  // 导入 service
// 导入 font-awesome
import "font-awesome/css/font-awesome.min.css";
import * as echarts from "echarts";             // 导入 echarts npm install echarts --save


// 注册 element-ui
Vue.use(ElementUI);


Vue.prototype.service = service; // 挂载到原型 Vue 上，可全局使用
Vue.prototype.$echarts = echarts;  // 挂载到原型 Vue 上，可全局使用

// 导入 router
import router from "./router";

Vue.config.productionTip = false;

new Vue({
  router, // 挂载路由
  render: (h) => h(App),
}).$mount("#app");
