// 配置路由
import Vue from 'vue'
import Router from 'vue-router'

// 导入各个页面组件
// import Home from '../components/Home.vue' // 导入首页组件

Vue.use(Router) // 全局注册 Router

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
      hidden: true,
      component: resolve => require(['@/components/Login'], resolve)
    },
    {
      path: '/login',
      name: 'LoginView',
      hidden: true,
      component: resolve => require(['@/components/Login'], resolve)
    },
    {
      path: '*',
      name: 'NotFound',
      hidden: true,
      component: resolve => require(['@/components/NotFound'], resolve)
    },
    // {
    //   path: '/home',             // 路由路径, '/' 代表根路径，指向首页
    //   // component: Home       // 路由对应的组件
    //   // 路由懒加载
    //   // component: () => import('@/components/Home')
    //   component: resolve => require(['@/components/Home'], resolve)
    // },
    {
      path: '/home',             // 路由路径, '/' 代表根路径，指向首页
      name: '学生管理',
      iconClass: 'fa fa-users',
      redirect: '/home/student',
      component: () => import('@/components/Home'),
      children: [
        {
          path: '/home/student',
          name: '学生列表',
          iconClass: 'fa fa-list',
          component: () => import('@/components/students/StudentList')
        },
        {
          path: '/home/info',
          name: '信息列表',
          iconClass: 'fa fa-list-alt',
          component: () => import('@/components/students/InfoList')
        },
        {
          path: '/home/infos',
          name: '信息管理',
          iconClass: 'fa fa-list-alt',
          component: () => import('@/components/students/InfoLists')
        },
        {
          path: '/home/work',
          name: '作业列表',
          iconClass: 'fa fa-list-ul',
          component: () => import('@/components/students/WorkList')
        },
        {
          path: '/home/works',
          name: '作业管理',
          iconClass: 'fa fa-th-list',
          component: () => import('@/components/students/WorkMent')
        },
      ]
    },
    {
      path: '/home',             // 路由路径, '/' 代表根路径，指向首页
      name: '数据分析',
      iconClass: 'fa fa-bar-chart',
      component: () => import('@/components/Home'),
      children: [
        {
          path: '/home/dataview',
          name: '数据概览',
          iconClass: 'fa fa-line-chart',
          component: () => import('@/components/dataAnalysis/DataView')
        },
        {
          path: '/home/mapview',
          name: '地图概览',
          iconClass: 'fa fa-line-chart',
          component: () => import('@/components/dataAnalysis/MapView')
        },
        {
          path: '/home/score',
          name: '分数地图',
          iconClass: 'fa fa-line-chart',
          component: () => import('@/components/dataAnalysis/ScoreMap')
        },
        {
          path: '/home/travel',
          name: '旅游地图',
          iconClass: 'fa fa-line-chart',
          component: () => import('@/components/dataAnalysis/TravelMap')
        },

      ]
    },
    {
      path: '/users',             // 路由路径, '/' 代表根路径，指向首页
      name: '用户中心',
      iconClass: 'fa fa-user',
      component: () => import('@/components/Home'),
      children: [
        {
          path: '/users/user',
          name: '权限管理',
          iconClass: 'fa fa-user',
          component: () => import('@/components/users/UserView')
        },
      ]
    }
  ],
  mode: 'history' // 去掉#号，mode 设置为 history，可以让路由支持 HTML5 History API
})