import axios from "axios";
import { getToken } from "./setToken.js";
import { Promise } from "core-js";
import { Message } from "element-ui";

const service = axios.create({
  baseURL: "/api", // baseUrl 会自动添加前缀，
  timeout: 3000, // 请求超时时间
});

// 添加请求拦截器
service.interceptors.request.use(
  (config) => {
    // 在发送请求之前做些什么(获取并设置 token)
    console.log("service.js: 在发送请求之前做些什么");
    console.log("设置 token");
    config.headers["token"] = getToken("token");
    return config;
  },
  (error) => {
    // 对请求错误做些什么
    console.log("service.js: 对请求错误做些什么");
    return Promise.reject(error);
  }
);

// 添加响应拦截器
service.interceptors.response.use(
  (response) => {
    // 对响应数据做点什么
    console.log("service.js: 对响应数据做点什么");
    console.log("相应拦截器的数据", response.data);
    let { status, message } = response.data;
    console.log(status, message);
    if (status !== 200) {
      Message({ message: message || "Error", type: "warning" });
    }
    return response; // 必须返回 response
  },
  (error) => {
    // 对响应错误做点什么
    console.log("service.js: 对响应错误做点什么");
    return Promise.reject(error);
  }
);

export default service;
